body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#map {
	margin-top: 20px;
}


#container {
	margin-left : 2%;
	margin-right : 2%;
	margin-top: 3%;

}

#header {
	background-color: #4973AB;
	top: 0px;
	width: 100%;
}

#title {
	color: white;
	padding-top: 20px;
	padding-left: 10px;
}

#filterBlock{
	margin-top: 20px;
	padding-top: 10PX;
	padding-bottom: -10px;
	width: 100%;
	background-color: #DADADA;
}




@media screen and (min-width : 320px) and (max-width : 480px) {
  #container {
	margin-left : 2%;
	margin-right : 2%;
	padding-top: 5px;

	}

	#map {
		margin-top: 20px;
	}
}

@media screen and (min-width : 480px) and (max-width : 768px) {
  #container {
	margin-left : 2%;
	margin-right : 2%;
	padding-top: 100px;

	}

	#map {
		margin-top: 20px;
	}
}

@media screen and (min-width : 768px) and (max-width : 1024px) {
  #container {
	margin-left : 2%;
	margin-right : 2%;
	padding-top: 100px;

	}

	#header { position: fixed; }

	#map {
		margin-top: 20px;
	}

	#restList { margin-left: 50%; }

	#filter_and_map {
		position: fixed;
	}
}

@media screen and (min-width : 1440px) and (max-width : 2560px) {
  #container {
	margin-left : 1%;
	margin-right : 1%;
	padding-top: 100px;

	}

	#header { position: fixed; }

	#map {
		margin-top: 20px;
	}

	#restList { margin-left: 50%; }

	#filter_and_map {
		position: fixed;
	}
}
#mapContainer {
	height: 500px;
	width: 100%;
}

@media screen and (min-width : 320px) and (max-width : 480px) {
